.pagination-list{
    margin-top: 60;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10;
}

.paginatio-input-list{
    margin-top: 60;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10;
}

.pgn-current{
    background-color: #ff6b08;
    color: #ffffff;
    border: none;
}

.pgn-btn{
    min-width: 40;
    height: 40;
    border-radius: 5;
    border-color: transparent;
}

.pgn-btn:hover{
    background-color: #ff6b08;
    color: #ffffff;
    fill: #ffffff
}

.next:hover{
    background-color: #ff6b08;
    color: #ffffff;
    fill: #ffffff
}

.pgn-dots{
    display: none;
}

.last-number{
    display: none;
}

.first-number{
    display: none;
}


@media screen and (min-width: 768px) {
    .pgn-dots{
        display: block;
    }
  
.pagination-list {
  margin-top: 60;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10;
}

.pgn-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 40;
  height: 40;
  border-radius: 5;
  border-color: transparent;
  border-width: 3px;
  background-color: transparent;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

